nav {
  box-shadow: 0px 0px 30px 2px rgba(0, 0, 0, 0.6);
  border-bottom: 1px solid transparent;
  background: rgba(145, 199, 44, 0.9);
  border-color: rgba(0, 0, 0, 0.1);
  display: flex;
  width: 100%;
  height: 70px;
  position: fixed;
  font-size: 1.1rem;
  transition: 600ms cubic-bezier(0.68, -0.55, 0.265, 1.55);
  z-index: 1100;
  justify-content: space-around;
}

nav .nav-full {
  position: relative;
  top: 30px;
}

nav .nav-full ul {
  width: 100%;
  display: flex;
  justify-content: space-around;
}

nav .nav-full ul li {
  list-style: none;
}

nav .nav-full ul li a i {
  color: white;
}

nav .nav-full ul li a.principal {
  text-decoration: none;
  color: #fefefe;
  font-weight: bold;
  padding: 15px;
  transition: 500ms;
  position: relative;
  top: 0px;
  padding-top: 35px;
  overflow: hidden;
}

nav .nav-full ul li a.principal:hover {
  color: #3767A6;
  background: linear-gradient(to bottom, #8404ac 0%, #380248 100%);
  background-color: #5E037A;
  color: #F9FDFC;
}

nav .nav-full ul li a.principal:hover::before {
  width: 100%;
}

nav .nav-full ul li a.arreglos:hover + .sub-nav {
  visibility: visible;
  opacity: 1;
}

nav .sub-nav {
  transition: all 500ms;
  z-index: 900;
  position: fixed;
  width: 100%;
}

nav .sub-nav:hover {
  visibility: visible;
  opacity: 1;
}

nav .sub-nav:hover + .arreglos {
  border-right: solid 2px lig(#5E037A, 10%);
  border-left: solid 2px #380248;
}

nav .sub-nav ul {
  padding: 5px;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

nav .sub-nav ul li {
  transition: 500ms;
  margin: 5px;
}

nav .sub-nav ul li:hover {
  background: rgba(87, 131, 208, 0.7);
  transform: scale(1.1, 1.2);
  box-shadow: 0px 0px 15px rgba(0, 0, 0, 0.3);
}

nav .sub-nav ul li a {
  display: block;
  text-align: center;
  color: #fefefe;
  font-weight: bold;
  transition: all 500ms;
}

nav .sub-nav ul li a:hover {
  color: #F9FDFC;
}

nav .sub-nav ul li a:hover::before {
  width: 100%;
}

